@import url(https://fonts.googleapis.com/css?family=Raleway:400,500%27);
@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  position: absolute;
  width: 100vw;
  height: 110vh;
  overflow: hidden;
  background-color: rgb(12, 12, 12);
}

body::after {
  content:"";
  position: absolute;
  width: 200vw;
  height: 200vh;
  left: -50%;
  top: -50%;
  background-image: url("http://api.thumbr.it/whitenoise-361x370.png?");
  z-index: -1;
  -webkit-animation:grain 1.5s steps(6) infinite;
          animation:grain 1.5s steps(6) infinite;
}

@-webkit-keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}

@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}


@font-face {
  font-family: "SSF";
  src: url(/yeezy-quote-machine/static/media/SandsGreyhoodSeven.54594856.otf);
}

#quote-box {
  margin: auto;
  margin-top: 5%;
  width: 80%;
  height: 100%;
  align-self: center;
  border-radius: 10px;
  padding: 20px 25px 15px 25px;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end;
}

.twitter-share-button {
  align-items: flex-start;
  width: 41px;
  margin-right: 90%;
  margin-bottom: 3%;
}

#twitter-img {
  width: 40px;
  opacity: 0.5;
  align-items: flex-start;
}

#text,
#author {
  font-family: "SSF";
  font-weight: unset;
  font-size: xxx-large;
}

#author {
  text-align: right;
  font-size: xx-large;
}

.button {
  font-family: "SSF", sans-serif;
  font-size: xx-large;
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-decoration: none;

  border: 4px solid rgba(245, 214, 39, 0.5);
  color: rgba(94, 82, 13, 0.5);
  background: rgba(255, 255, 255, 0.247);
  outline: none;

  display: inline-block;
  position: relative;
  padding: 15px 10px 14px;
  cursor: pointer;
  width: auto;
}

.button:hover {
  top: 2px;
  left: 2px;
}
.button:hover:after {
  top: -2px;
  left: -2px;
}

a {
  text-decoration: none;
}

a:hover {
  color: #8a8a8a;
}

a:hover .code {
  color: #cc9f22;
}

.code {
  color: #ffffff;
  transition: color 0.3s ease-out;
}

footer {
  padding: 14px;
  color: #a3a3a3;
  font-family: "SSF";
  display: flex;
  flex-flow: column;
  align-items: flex-end;
  text-align: left;
}

p {
  margin: 5px;
}

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (max-device-width: 811px){
  #quote-box {
    width: 150%;
    position: absolute;
    height: inherit;
    align-self: center;
    border-radius: 10px;
    padding: 20px 25px 15px;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-end;
    font-size: 2em;
    margin-top: 5em;
}
body {
    width: 100vw;
    height: 110vh;
    overflow: visible;
    background-color: #0c0c0c;
}
body:after {
    content: "";
    width: 500vh;
    height: 500vh;
    position: absolute;
    top: -50%;
    right: -100%;
    -webkit-animation: none;
            animation: none;
    background-image: url(http://api.thumbr.it/whitenoise-361x370.png);
    z-index: -1;
}

#author, #text, .button {
    font-size: 2em;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    justify-content: space-around;
}

.button {
	border: 0.1em solid rgba(245,214,39,.5);
}

#author, .button {
    font-size: 1.5em;
}

#twitter-img {
    width: 2em;
}

footer {
font-size: 1.3em;
}
}


